import React, { FormEvent, useEffect, useState } from 'react'
import { Button, Col, Form, Row } from 'react-bootstrap'
import { useDispatch, useSelector } from 'react-redux'
import { Link } from 'react-router-dom'
import FormContainer from '../components/FormContainer'
import Loading from '../components/Loading'
import Message from '../components/Message'
import { State } from '../models/State'
import { register } from '../redux/actions/user'

const RegisterScreen = ({ location, history }: any) => {
  const [email, setEmail] = useState('');
  const [password, setPassword] = useState('');
  const [name, setName] = useState('');
  const [confirmPassword, setConfirmPassword] = useState('');
  const [message, setMessage] = useState('');

  const userRegister = useSelector((state: State) => state.userRegister);
  const { loading, user, error } = userRegister;

  const redirect = location.search ? location.search.split('=')[1] : '/';

  const dispatch = useDispatch();

  const submitHandler = (e: FormEvent) => {
    e.preventDefault();

    if (password !== confirmPassword) {
      setMessage('');
    } else {
      dispatch(register(name, email, password));
    }

  }

  useEffect(() => {
    if (user) {
      history.push(redirect)
    }
  }, [history, user, redirect])

  return (
    <FormContainer>
      <h1></h1>
      { message && <Message variant='danger'>{message}</Message>}
      { error && <Message variant='danger'>{error}</Message>}
      { loading && <Loading />}
      <Form onSubmit={submitHandler}>
        <Form.Group controlId='name'>
          <Form.Label>:</Form.Label>
          <Form.Control
            type='text'
            placeholder=''
            value={name}
            onChange={(e) => setName(e.target.value)}>
          </Form.Control>
        </Form.Group>`

        <Form.Group controlId='email'>
          <Form.Label>:</Form.Label>
          <Form.Control
            type='email'
            placeholder=''
            value={email}
            onChange={(e) => setEmail(e.target.value)}>
          </Form.Control>
        </Form.Group>

        <Form.Group controlId='password'>
          <Form.Label>:</Form.Label>
          <Form.Control
            type='password'
            placeholder=''
            value={password}
            onChange={(e) => setPassword(e.target.value)}>
          </Form.Control>
        </Form.Group>

        <Form.Group controlId='confirmPassword'>
          <Form.Label>:</Form.Label>
          <Form.Control
            type='password'
            placeholder=''
            value={confirmPassword}
            onChange={(e) => setConfirmPassword(e.target.value)}>
          </Form.Control>
        </Form.Group>
        <Button type='submit' variant='primary'></Button>
      </Form>

      <Row className='py-3'>
        <Col>
          
          <Link to={redirect ? ('/login?redirect=' + redirect) : '/login'}></Link>
        </Col>
      </Row>

    </FormContainer>
  )

}

export default RegisterScreen;
