import React, { FormEvent, useEffect, useState } from 'react'
import { Button, Col, Form, Row } from 'react-bootstrap'
import { useDispatch, useSelector } from 'react-redux'
import Loading from '../components/Loading'
import Message from '../components/Message'
import { State } from '../models/State'
import User from '../models/User'
import { getUserDetails, updateUserDetails } from '../redux/actions/user'

const ProfileScreen = ({ history }: any) => {
  const [email, setEmail] = useState('');
  const [password, setPassword] = useState('');
  const [name, setName] = useState('');
  const [confirmPassword, setConfirmPassword] = useState('');

  const dispatch = useDispatch();

  const userDetails = useSelector((state: State) => state.userDetails);
  const { loading, user, error, updated } = userDetails;

  const userLogin = useSelector((state: State) => state.userLogin);
  const loginUser = userLogin.user;

  useEffect(() => {
    if (!loginUser) {
      history.push('/login')
    } else {
      if (!user || (user && !user.name)) {
        if (!updated && user) dispatch(getUserDetails(user.id))
      } else {
        setName(user.name);
        setEmail(user.email);
      }
    }
  }, [dispatch, history, user, loginUser, updated]);

  const submitHandler = (e: FormEvent) => {
    e.preventDefault();
    dispatch(updateUserDetails(user ? new User(user.id, name, email, user.token, password): null))
  }

  return (
    <Row>
      <Col md={3}>
        <h2>  </h2>
        {!error && updated && <Message variant='success'>Update Success</Message>}
        {error && <Message variant='danger'>{error}</Message>}
        {loading && <Loading />}
        <Form onSubmit={submitHandler}>
          <Form.Group controlId='name'>
            <Form.Label>:</Form.Label>
            <Form.Control
              type='text'
              placeholder=''
              value={name}
              onChange={(e) => setName(e.target.value)}>
            </Form.Control>
          </Form.Group>`

        <Form.Group controlId='email'>
            <Form.Label>:</Form.Label>
            <Form.Control
              type='email'
              placeholder=''
              value={email}
              onChange={(e) => setEmail(e.target.value)}>
            </Form.Control>
          </Form.Group>

          <Form.Group controlId='password'>
            <Form.Label>:</Form.Label>
            <Form.Control
              type='password'
              placeholder=''
              value={password}
              onChange={(e) => setPassword(e.target.value)}>
            </Form.Control>
          </Form.Group>

          <Form.Group controlId='confirmPassword'>
            <Form.Label>:</Form.Label>
            <Form.Control
              type='password'
              placeholder=''
              value={confirmPassword}
              onChange={(e) => setConfirmPassword(e.target.value)}>
            </Form.Control>
          </Form.Group>
          <Button type='submit' variant='primary'></Button>
        </Form>
      </Col>
      <Col md={9}>
        <h2></h2>
      </Col>
    </Row>
  )

}

export default ProfileScreen;
